import React from "react";

const Welcome = () => {
  return (
    <div className="br3 ba mt2 dark-gray b--black-10 w-100-m w-80 shadow-5 center">
      <h1 className="tc">Vlkommen till Esmia!</h1>
      <p className="pa3 tl">
        Vi sljer spanska delikatesser. Efter 20 r i branschen kan vi bekrfta
        att det r kvalitet som sljer!
      </p>
      <p className="pa3 tl">
        Vrt lager bestr av kylvaror, ssom ibericoskinka, serranoskinka,
        chorizo, lagrad manchego, frsk getost, sherryvinger, olivolja,
        pimentos piquillo, marconamandlar med mera.
      </p>
      <p className="pa3 tl">
        Vra priser stts med lg marginal i frhllande till importpriset.
        Nedan hittar ni vra produkter.
      </p>
    </div>
  );
};

export default Welcome;



