import tapasJamon from "./images/tapasJamon.jpg";
import tjockskinka from "./images/tjockskinka.jpg";

const Skinkor = [
  {
    name: "Iberico ben, 30 mn",
    img: tapasJamon,
    price: "330kr/kg",
    description:
      "Ibericoskinkan r lufttorkad i 30 mnader. Gris av svart ras. Den avlas i naturen med ekollon och andra rter. 6 mnader innan slakt utfodras grisarna med vanligt foder. 7,5kg /st."
  },
  {
    name: "Iberico, vac., 30 mn",
    img: tjockskinka,
    price: "450kr/kg",
    description:
      "Ibericoskinka 30 mnaders lufttorkad. Den svarta grisrasen avlas i naturen med ekollon och rter. Senaste mnader ter den vanlig grisfda, recebo. Vikt i vacuumfrp.: 4-5kg /st."
  },
  {
    name: "Chorizo och Salami Iberico",
    img: tjockskinka,
    price: "175kr/kg",
    description:
      "Chorizo och Salchichon (salame) Iberico charkuteri. Mycket smak och doft av svart den svarta grisrasen Iberico. Serveras kallskuret p fat med paprika, oliver mm. Frpackning: 1,2kg st. 6 st/kolli = 8kg"
  },
  {
    name: "Serranoskinka reserva 14 mn",
    img: tjockskinka,
    price: "160kr/kg",
    description:
      "Chorizo och salchichon (salame) Iberico charkuteri. Mycket smak och doft av svart den svarta grisrasen Iberico. Serveras kallskuret p fat med paprika, oliver mm. Frpackning: 1,2kg st. 6 st/kolli = 8kg"
  },
  {
    name: "Serranoskinka 500g skivad",
    img: tjockskinka,
    price: "235kr/kg",
    description:
      "Serranoskinka skivad 500gr. Ltt att dra varje skiva med mellanlgg. Skinkan frn grishona med 11 mnaders lufttorkning. Linda den med dadlar, runt gettjockskinka, servera med oliver. Frpackning: 5 st x 500g = 2,5kg/kolli."
  }
];

export default Skinkor;



